import React, {Component} from "react";
import {Router, Route, Switch, Redirect} from "react-router-dom";
import indexRoutes from "./routes";
import {createBrowserHistory} from "history";
import {ToastContainer} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './App.css';
import $ from "jquery";
import http from "axios";
import intl from "react-intl-universal";

const hist = createBrowserHistory();

class App extends Component {
    state = {initDone: false};

    componentDidMount() {
        if (!localStorage.getItem('lang'))
            localStorage.setItem('lang', 'fa');
        this.loadLanguage();
    }

    loadLanguage() {
        let currentLocale = localStorage.getItem('lang');
        $("html").attr("lang", currentLocale);
        if (currentLocale === 'fa') {
            let $url = window.location.origin;
            $("body").attr("dir", "rtl");
            let $head = $("head");
            let $headlinklast = $head.find("style:last");
            let linkElement = "<link rel='stylesheet' type='text/css' href='" + $url + "/assets/css/rtl.css' /><link rel='stylesheet' type='text/css' href='" + $url + "/style-rtl.css' />";
            if ($headlinklast.length) {
                $headlinklast.after(linkElement);
            }
            else {
                $head.append(linkElement);
            }
            let $body = $("body");
            let $bodylinklast = $body.find("script[type='text/javascript']:last");
            let scriptElement = "<script type='text/javascript' href='assets/js/rtl.js' />";
            if ($bodylinklast.length) {
                $bodylinklast.after(scriptElement);
            }
            else {
                $body.append(scriptElement);
            }
            console.log('append');
        } else {
            $("body").attr("dir", "ltr");
        }

        http
            .get(window.location.origin + `/i18n/${currentLocale}.json`)
            .then(res => {
                return intl.init({
                    currentLocale,
                    locales: {
                        [currentLocale]: res.data
                    }
                });
            }).then(() => {
            this.setState({ initDone: true });
        });
    }

    render() {
        return (
            this.state.initDone &&
            <div>
                <ToastContainer
                    position="bottom-left"
                    autoClose={5000}
                    hideProgressBar={false}
                    newestOnTop={false}
                    closeOnClick
                    rtl={false}
                    pauseOnVisibilityChange
                    draggable
                    pauseOnHover
                />
                <Router history={hist}>
                    <Switch>
                        {indexRoutes.map((prop, key) => {
                            if (prop.redirect)
                                return <Redirect from={prop.path} to={prop.to} key={key}/>;
                            return <Route path={prop.path} component={prop.component} key={key}/>;
                        })}
                    </Switch>
                </Router>
            </div>
        );
    }

}

export default App;



