import React, {Component} from 'react';
import {toast} from 'react-toastify';
import intl from 'react-intl-universal';
import {Card, Row, Col} from 'antd';
import axios from 'axios';
// import config from "react-global-configuration";
// import Colorrose from './Colorrose';
// import _ from 'lodash';

const {Meta} = Card;

class HomePage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            team: {fa: [], en: []},
            about: {fa: null, en: null}
        };
    }

    componentDidMount() {
        axios.get(window.location.origin + `/data/about.json`)
            .then(res => {
                this.setState({about: res.data.about})
            }).catch(error => {
            toast.error('About not loading :(');
        });
        axios.get(window.location.origin + `/data/team.json`)
            .then(res => {
                this.setState({team: res.data})
            }).catch(error => {
            toast.error('Team not loading :(');
        });
    }


    render() {
        let lang = localStorage.getItem('lang');
        let {about, team} = this.state;
        return (
            <div>
                <hr/>
                <h1>

                </h1>
                <Card
                    style={{width: "100%"}}
                >
                    <Meta
                        title={intl.get('HOME.aroin')}
                        description={about[lang]}
                    />
                </Card>
                <br/>
                <b>{intl.get('HOME.about_us')}</b>
                <Row gutter={16}>
                    {team[lang].map((person, index) =>
                        <Col key={index} sm={6}>
                            <Card
                                hoverable
                                style={{width: 280, margin: 5}}
                                cover={<img alt="example" src={person.avatar}/>}
                                actions={[<b>{person.post}</b>]}
                            >
                                <Meta
                                    title={person.name + " " + person.family}
                                    description={person.description}
                                />
                            </Card>
                        </Col>)}
                </Row>
            </div>
        );
    }
}

export default HomePage;



