import { Menu, Dropdown, Button, Icon, message } from 'antd';
import {Component} from "react";
import React from "react";
import http from "axios";
import intl from "react-intl-universal";
import $ from 'jquery';
import _ from 'lodash';

const SUPPOER_LOCALES = [
    {
        name: "English",
        value: "en"
    },
    {
        name: "",
        value: "fa"
    }
];

export default class LangSwitch extends Component {
    state = {
        lang: ''
    };

    componentDidMount() {
        this.setState({lang: _.find(SUPPOER_LOCALES, {value: localStorage.getItem('lang')}).name})
    }


    handleMenuClick = (e) => {
        message.info('Change Language to ' + _.find(SUPPOER_LOCALES, {value: e.key}).name);
        this.changeLanguage(e.key);
        this.setState({ lang: _.find(SUPPOER_LOCALES, {value: e.key}).name})
    };

    changeLanguage(lang) {
        localStorage.setItem('lang', lang);
        this.loadLocales();
    }

    loadLocales() {
        let currentLocale = localStorage.getItem('lang');
        $("html").attr("lang", currentLocale);
        if (currentLocale === 'fa') {
            let $url = window.location.origin;
            $("body").attr("dir", "rtl");
            let $head = $("head");
            let $headlinklast = $head.find("style:last");
            let linkElement = "<link rel='stylesheet' type='text/css' href='" + $url + "/assets/css/rtl.css' /><link rel='stylesheet' type='text/css' href='" + $url + "/style-rtl.css' />";
            if ($headlinklast.length) {
                $headlinklast.after(linkElement);
            }
            else {
                $head.append(linkElement);
            }
            let $body = $("body");
            let $bodylinklast = $body.find("script[type='text/javascript']:last");
            let scriptElement = "<script type='text/javascript' href='assets/js/rtl.js' />";
            if ($bodylinklast.length) {
                $bodylinklast.after(scriptElement);
            }
            else {
                $body.append(scriptElement);
            }
            console.log('append');
        } else {
            $("body").attr("dir", "ltr");
        }

        http
            .get(window.location.origin + `/i18n/${currentLocale}.json`)
            .then(res => {
                return intl.init({
                    currentLocale,
                    locales: {
                        [currentLocale]: res.data
                    }
                });
            }).then( () => {
            window.location.reload();
        });
    }


    render() {
        const {lang} = this.state;
        const menu = (
            <Menu onClick={this.handleMenuClick}>
                {SUPPOER_LOCALES.map( lang =>
                    <Menu.Item key={lang.value}><Icon type="global" />{lang.name}</Menu.Item>
                )}
            </Menu>
        );
        return (
            <Dropdown overlay={menu}>
                <Button style={{ marginLeft: 8 }}>
                    {lang} <Icon type="down" />
                </Button>
            </Dropdown>
        );
    }
}


