import React, {Component} from 'react';
import intl from 'react-intl-universal';
import {Layout, Menu} from 'antd';
import {
    Link
} from 'react-router-dom';
import LangSwitch from "../../core/multilingual/multilingual";

const {Header} = Layout;


export class HeaderLayout extends Component {

    render() {
        return (
            <Header>
                <div className="logo"/>
                <Menu
                    theme="dark"
                    mode="horizontal"
                    defaultSelectedKeys={['1']}
                    style={{lineHeight: '64px'}}
                >
                    <Menu.Item key="1"><Link to="">{intl.get('MENU.home')}</Link></Menu.Item>
                    <Menu.Item key="2"><Link to="/about">{intl.get('MENU.about')}</Link></Menu.Item>
                    <Menu.Item key="3"><Link to="/news">{intl.get('MENU.news')}</Link></Menu.Item>
                    <Menu.Item key="4"><Link to="/timeline">{intl.get('MENU.timeline')}</Link></Menu.Item>
                    <Menu.Item key="5"><a href="https://github.com/aroin"> {intl.get('MENU.github')}</a></Menu.Item>
                    <Menu.Item key="6"><a href="https://t.me/aroin_ir">{intl.get('MENU.telegram')}</a></Menu.Item>
                    <LangSwitch />
                </Menu>
            </Header>
        );
    }
}



