import HomePage from "../pages/master/home/home";
import ContactPage from "../pages/master/contact/contact";
import About from "../pages/master/about/about";
import NewsPage from "../pages/master/news/news";
import TimelinePage from "../pages/master/timeline/timeline";


const masterRoutes = [
    {
        path: "/",
        sidebarName: "Home Page",
        navbarName: "Home Page",
        icon: "home",
        component: HomePage
    },
    {
        path: "/contact",
        sidebarName: "Contact",
        navbarName: "Contact",
        icon: "contact",
        component: ContactPage
    },
    {
        path: "/about",
        sidebarName: "Map",
        navbarName: "Map",
        icon: "map",
        component: About
    },
    {
        path: "/news",
        sidebarName: "news",
        navbarName: "news",
        icon: "news",
        component: NewsPage
    },
    {
        path: "/timeline",
        sidebarName: "timeline",
        navbarName: "timeline",
        icon: "news",
        component: TimelinePage
    }
];

export default masterRoutes;


