import React, {Component} from 'react';
import SimpleMap from './simple-map';
import intl from 'react-intl-universal';
import {Card, Icon} from "antd";
import axios from "axios";
import {toast} from "react-toastify";
import './map.css';

const {Meta} = Card;


const aboutCard = (about, address, telephone) => {
    let lang = localStorage.getItem('lang');
    return <div>
        <p>{about[lang]}</p>
        <Icon type="compass" theme="outlined"/> : {address[lang]} <br/>
        <Icon type="phone" theme="outlined"/> : {telephone[lang]}
    </div>
};

export default class About extends Component {

    constructor(props) {
        super(props);
        this.handleMapClick = this.handleMapClick.bind(this);
        this.state = {
            about: {fa: null, en: null},
            address: {fa: null, en: null},
            telephone: {fa: null, en: null}
        };
    }

    componentDidMount() {
        axios.get(window.location.origin + `/data/about-us.json`)
            .then(res => {
                this.setState({about: res.data.aroin, address: res.data.address, telephone: res.data.telephone})
            }).catch(error => {
            toast.error('About not loading :(');
        });
    }

    handleMapClick(param) {
        // alert(param);
    }

    render() {
        let {about, address, telephone} = this.state;
        let center = [35.58151070512222, 53.388844728469856];
        let popupText = "<b>" + intl.get('APP.title') + "</b>";
        let markerLocation = [35.58151070512222, 53.388844728469856];
        let handleMapClick = this.handleMapClick;
        return (
            <div>
                <Card
                    style={{width: "100%"}}
                >
                    <Meta
                        title={intl.get('ABOUT.about_more')}
                        description={aboutCard(about, address, telephone)}
                    />
                </Card>
                <SimpleMap center={center} popupText={popupText} markerLocation={markerLocation}
                           mapClick={handleMapClick}/>
            </div>
        )
    }
}




