import React, {Component} from 'react';
import {Button} from 'antd';
import intl from 'react-intl-universal';
import axios from "axios";
import {toast} from "react-toastify";
import {Icon} from 'antd';


class NewsPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            news: [],
            count: 0
        }
    }

    componentDidMount() {
        axios.get(window.location.origin + `/data/news.json`)
            .then(res => {
                this.setState({news: res.data})
            }).catch(error => {
            toast.error('About not loading :(');
        });
    }


    render() {
        let lang = localStorage.getItem('lang');

        const postImage = (image) => {
            if (image) return <img className="img-fluid" aria-hidden src={image} alt="Sample image"/>;
            else return <img className="img-fluid" aria-hidden src="image/cover.jpeg" alt="Sample image"/>;
        };

        const posts = this.state.news.map((post, index) =>
            <div className="row" key={index}>
                <div className="col-lg-5">
                    <div className="jumbotron">
                        {postImage(post[lang].image)}
                    </div>
                </div>
                <div className="col-lg-7">
                    <a className="green-text"><h6 className="font-weight-bold mb-3"><Icon type="tags"
                                                                                          theme="outlined"/>{post[lang].tags}
                    </h6></a>
                    <h3 className="font-weight-bold mb-3 p-0"><strong>{post.title}</strong></h3>
                    <p>{post[lang].body}</p>
                    <p>by <a><strong>{post[lang].author}</strong></a>, {post[lang].publishedAt}</p>
                    {post[lang].url ? <Button href={post[lang].url} type="primary"
                                        className="waves-light ">{intl.get('HOME.read_more')}</Button> : null}
                </div>
            </div>
        );
        return (
            <div>
                {posts}
            </div>
        );
    }
}

export default NewsPage;



