import React from "react";
import {Switch, Route, Redirect} from "react-router-dom";
import {FooterLayout, HeaderLayout} from "./partial";
import './master-layout.css';
import masterRoutes from "../routes/master";
import config from 'react-global-configuration';
import { Layout, Breadcrumb } from 'antd';
import intl from 'react-intl-universal';
const { Content } = Layout;

const switchRoutes = (
    <Switch>
        {masterRoutes.map((prop, key) => {
            if (prop.redirect)
                return <Redirect from={prop.path} to={prop.to} key={key}/>;
            return <Route path={prop.path} exact={true} component={prop.component} key={key}/>;
        })}
    </Switch>
);

class Master extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            mobileOpen: false
        };
        this.resizeFunction = this.resizeFunction.bind(this);
        if (config.get('config'))
            window.document.getElementsByTagName("title")[0].innerHTML = intl.get('APP.title')
    }

    handleDrawerToggle = () => {
        this.setState({mobileOpen: !this.state.mobileOpen});
    };

    resizeFunction() {
        if (window.innerWidth >= 960) {
            this.setState({mobileOpen: false});
        }
    }

    componentDidMount() {
        window.addEventListener("resize", this.resizeFunction);
    }

    componentDidUpdate(e) {
        if (e.history.location.pathname !== e.location.pathname) {
            this.refs.mainPanel.scrollTop = 0;
            if (this.state.mobileOpen) {
                this.setState({mobileOpen: false});
            }
        }
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.resizeFunction);
    }

    render() {
        const {classes, ...rest} = this.props;
        return (
            <Layout className="layout">
                <HeaderLayout
                    routes={masterRoutes}
                    handleDrawerToggle={this.handleDrawerToggle}
                    {...rest}
                />
                <Content style={{padding: '0 50px'}} ref="mainPanel">
                    <Breadcrumb style={{margin: '16px 0'}}>
                        <Breadcrumb.Item>{intl.get('MENU.home')}</Breadcrumb.Item>
                        {/*<Breadcrumb.Item>List</Breadcrumb.Item>*/}
                        {/*<Breadcrumb.Item>App</Breadcrumb.Item>*/}
                    </Breadcrumb>
                    <div style={{background: '#fff', padding: 24, minHeight: 280}}>{switchRoutes}</div>
                </Content>
                <FooterLayout/>
            </Layout>
        );
    }
}

export default Master;



