import React, {Component} from 'react';
// import intl from 'react-intl-universal';
import axios from "axios";
import {toast} from "react-toastify";
import { Timeline } from 'antd';


class TimelinePage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            timeline: []
        }
    }

    componentDidMount() {
        axios.get(window.location.origin + `/data/timeline.json`)
            .then(res => {
                this.setState({timeline: res.data})
            }).catch(error => {
            toast.error('About not loading :(');
        });
    }


    render() {
        let lang = localStorage.getItem('lang');

        return (
            <Timeline>
                {this.state.timeline.map((item, index) =>
                    <Timeline.Item key={index} color={item[lang].color}>{item[lang].description}</Timeline.Item>
                )}
            </Timeline>
        );
    }
}

export default TimelinePage;



